body {
  height: 100%;
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  font-family: "Inter", "Domine", sans-serif;
  color: #183b56;
  font-size: 16px;
  line-height: 1;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  @include response(1200px) {
    ::-webkit-scrollbar {
      display: none;
    }
  }
}

.wrapper {
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: hidden;
  flex: 1 1 auto;
  &--ovv {
    @include response-min(1201px) {
      overflow: visible;
    }
  }
}

.container {
  position: relative;
  max-width: 1240px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include response(1250px) {
    padding-left: 24px;
    padding-right: 24px;
  }
  &--small {
    max-width: 1020px;
  }
}

.main {
  position: relative;
  padding-top: 143px;
  padding-bottom: 90px;
  @include response(1200px) {
    padding-top: 104px;
    padding-bottom: 40px;
  }
}

* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
